package com.gsb.vyapar.bhagya.presentation.navigation.view

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.padding
import androidx.compose.material.BottomNavigation
import androidx.compose.material.Scaffold
import androidx.compose.material.rememberScaffoldState
import androidx.compose.runtime.Composable
import androidx.compose.runtime.rememberCoroutineScope
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.BlendMode.Companion.Color
import androidx.compose.ui.unit.dp
import androidx.navigation.compose.NavHost
import androidx.navigation.compose.composable
import androidx.navigation.compose.rememberNavController
import com.gsb.vyapar.bhagya.interactor.event.UiEvents
import com.gsb.vyapar.bhagya.interactor.uistate.UiState
import com.gsb.vyapar.bhagya.presentation.Screens.*
import com.gsb.vyapar.bhagya.presentation.category.add.view.AddCategoryView
import com.gsb.vyapar.bhagya.presentation.category.view.CategoriesView
import com.gsb.vyapar.bhagya.theme.*
import presentation.navigation.view.NavigationItem
import presentation.navigation.view.NavigationTopBar

@Composable
fun NavigationView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    Box(Modifier.fillMaxSize()) {
        val navController = rememberNavController()
        val scaffoldState = rememberScaffoldState()
        val scope = rememberCoroutineScope()
        Scaffold(
            scaffoldState = scaffoldState,
            topBar = {
                NavigationTopBar(navController, scaffoldState, scope)
            },
            drawerBackgroundColor = androidx.compose.ui.graphics.Color.Transparent,
            drawerContentColor = androidx.compose.ui.graphics.Color.Transparent,
            drawerElevation = 0.dp,
            drawerContent = {
                NavigationDrawerView(uiState, onTriggerEvent, navController)
            },
            bottomBar = {
                BottomNavigation(backgroundColor = primary) {
                    NavigationItem(Home, navController)
                    NavigationItem(Category, navController)
                    NavigationItem(Latest, navController)
                    NavigationItem(Favorites, navController)
                }
            }
        ) {
            NavHost(
                navController, startDestination = Home.toString(), modifier = Modifier
                    .background(color = navbackground)
                    .padding(it)
            ) {
                composable(Home.toString()) {
                    AddCategoryView(uiState, onTriggerEvent)
                }
                composable(Category.toString()) {
                    CategoriesView(uiState, onTriggerEvent)
                }
                composable(Latest.toString()) {

                }
                composable(Favorites.toString()) {

                }
            }
        }
    }
    PlatformBackHandler(uiState, onTriggerEvent)
}
