package com.gsb.vyapar.bhagya.domain

import kotlinx.serialization.Serializable
import kotlinx.serialization.encodeToString
import kotlinx.serialization.json.Json

@Serializable
data class SubCategories(
    val v: Int = 0,
    val d: D = D()
) {
    @Serializable
    data class D(
        val subCategories: List<SubCategory> = listOf()
    ) {
        @Serializable
        data class SubCategory(
            val id: Long = 0,
            val name: String = "",
            val categoryId: Long = 0,
            val deleted: Boolean = false,
        )
    }
}

fun String.decodeSubCategoriesString(): SubCategories {
    return Json.decodeFromString<SubCategories>(this)
}

fun SubCategories.encodeSubCategoriesString(): String {
    return Json.encodeToString(this)
}