package com.gsb.vyapar.bhagya.interactor.view

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.padding
import androidx.compose.material.Scaffold
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.layout.onSizeChanged
import androidx.compose.ui.unit.dp
import com.gsb.vyapar.bhagya.interactor.event.UiEvents
import com.gsb.vyapar.bhagya.interactor.uistate.UiState
import com.gsb.vyapar.bhagya.presentation.Screens
import com.gsb.vyapar.bhagya.presentation.common.animation.Animate
import com.gsb.vyapar.bhagya.presentation.dialog.view.DialogView
import com.gsb.vyapar.bhagya.presentation.navigation.view.NavigationView
import com.gsb.vyapar.bhagya.presentation.splash.view.SplashView
import com.gsb.vyapar.bhagya.theme.primary
import com.gsb.vyapar.bhagya.util.BuildConfig
import com.gsb.vyapar.bhagya.util.PlatformType

@Composable
fun MainView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    Scaffold() { paddingValues ->
        Box(Modifier.fillMaxSize().background(color = primary).padding(paddingValues).onSizeChanged {
            onTriggerEvent(UiEvents.Portrait(it.width < it.height))
        }) {
            Column(Modifier.padding(top = if (BuildConfig.platform() == PlatformType.IOS) 50.dp else 0.dp).padding(bottom = if (BuildConfig.platform() == PlatformType.IOS) 20.dp else 0.dp)) {
                when (uiState.mainView) {
                    Screens.Splash -> Animate { SplashView(uiState, onTriggerEvent) }
                    Screens.Navigation -> Animate { NavigationView(uiState, onTriggerEvent) }
                    else -> {}
                }
            }
            uiState.loadingUiState?.let { DialogView(it) }
        }
    }
}
