package com.gsb.vyapar.bhagya.presentation.dialog.view

import com.gsb.vyapar.bhagya.presentation.dialog.uistate.DialogUiState
import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.width
import androidx.compose.foundation.layout.wrapContentHeight
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.material.CircularProgressIndicator
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.text.font.FontWeight.Companion.SemiBold
import androidx.compose.ui.text.style.TextAlign.Companion.Center
import androidx.compose.ui.unit.dp
import androidx.compose.ui.unit.sp
import androidx.compose.ui.window.Dialog
import androidx.compose.ui.window.DialogProperties
import com.gsb.vyapar.bhagya.presentation.dialog.Dialogs.LOADING
import com.gsb.vyapar.bhagya.theme.background
import com.gsb.vyapar.bhagya.theme.interRegular
import com.gsb.vyapar.bhagya.theme.primary

@Composable
fun DialogView(dialogUiState: DialogUiState) {
    Dialog(
        onDismissRequest = { },
        properties = DialogProperties(
            dismissOnBackPress = false,
            dismissOnClickOutside = false,
            usePlatformDefaultWidth = false
        )
    ) {
        when (dialogUiState.dialogType) {
            LOADING -> {
                Column(
                    modifier = Modifier.width(260.dp).wrapContentHeight()
                        .background(background, shape = RoundedCornerShape(20.dp))
                        .padding(all = 22.dp), horizontalAlignment = Alignment.CenterHorizontally
                ) {
                    CircularProgressIndicator(color = primary)
                    Spacer(modifier = Modifier.height(20.dp))
                    Text(
                        text = dialogUiState.message,
                        color = primary,
                        fontFamily = interRegular(),
                        fontSize = 18.sp,
                        modifier = Modifier.padding(bottom = 20.dp).padding(start = 16.dp)
                            .padding(end = 16.dp),
                        textAlign = Center,
                        fontWeight = SemiBold
                    )
                }
            }
        }
    }
}